export function Client409s(){
  function Code(){
    return (<div >
  
  
  <div class="row">
<div>Aswa Stephen Thomas today the 29th of March, 2023 arrived Ndejje University Kampala campus for the <span style={{color:"red"}}>open air campaigns</span> that took place at the temporary structure and started at 2:00pm. Aswa arrived a little earlier at the venue at around 11:00am <span style={{color:"red"}}>and had brief interractions with majority of the students around campus.</span> </div><p></p>
<div class="col-md-3"></div>
<div class="col-md-6">
<img src={client409s} class=" d-block w-100" alt="Loading an image here...."  />
</div>
<div class="col-md-3"></div>
<div>Just above, After Aswa's engagement with majority of the students prior to the campaign time, he <span style={{color:"red"}}>sets off in his pajero van heading to the campaign venue</span> that was located about a quarter a kilometer from the campus.
<p></p>Aswa made his movement around the campus in his pajero van <span style={{color:"red"}}>waving all round to all the students</span> he wishes to represent as others were seen jumping and clinging on to the van as a way of enjoying the free lift from the aspirant.

</div>
<img src={client409s1} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s2} class=" d-block w-100" alt="Loading an image here...."  /><p></p>
<div>Aswa immediately after his procession all around the whole campus premises arrived the venue at exactly 2:30pm and was warmly welcomed with alot of vuvuzellers heard out blown by those who escorted him in the lead.</div>
<img src={client409s3} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s4} class=" d-block w-100" alt="Loading an image here...."  />
<div><span style={{color:"red"}}>Just above is Aswa's arrival to the venue</span>, he exited and majestically moved into the tent that was allocated for the candidates to take their seats.</div><p></p>
<img src={client409s5} class=" d-block w-100" alt="Loading an image here...."  />

<p></p>

<div>Aswa's in his arrival in the tent immediately sat <span style={{color:"red"}}>two seats away from his opponent Okoth Phillips, also a guild presidential aspirant. It was very awesome to observe the love for each other as aswa extended his arm to greet his competitor</span> in the same race before he took his seat.</div>
<img src={client409s6} class=" d-block w-100" alt="Loading an image here...."  />
<div>The event literally was on going as candidates for the race of GRCs and vice guild presidents were given first priority to present themselves. Aston's team arrived with disorganization at the scene and was fined shs. 100,000/= by the presider of the event as he mentioned in his speech that Aston should pay a fine of shs. 100,000/=. <p></p>As Aston, one who is racing for vice guild presidency was presenting himself, the crowd was seen reacting with negative responses and it looked like some of them wanted him off the stage. Aston's presentation was characterized with more noise than his presentation as many vuvuzellers interfered his communication. In the photo below, was a student who was noticed sending a signal that Aston should leave the stage. </div>
<img src={client409s7} class=" d-block w-100" alt="Loading an image here...."  />
<div><p></p>The photo below shows security trying to intervene asking the noisy vuvuzellers to be controlled as the situation was turning noisy in that the candidate's message was not being conveyed as expected. </div><p></p>

<img src={client409s8} class=" d-block w-100" alt="Loading an image here...."  />
<p></p><div>It was Aswa's turn to be on stage and the crowd rose up in shouts and louder jubilations heard than any other. Aswa matched majestically with an excited and smiling face that seemed to convey his happiness to the students he saw turn up because of his call.</div>
<img src={client409s9} class=" d-block w-100" alt="Loading an image here...."  />
<p></p><div>As he walked to the stage, he went through a short <span style={{color:"red"}}>series of hugs from some of the die hard supporters</span> who were noticed on ground</div>
<img src={client409s10} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s11} class=" d-block w-100" alt="Loading an image here...."  />
<div>Another lot of students rushed to the stage and rallied behind him as he prepared to receive a microphone to air out his speech. This is shown above.<p></p>As you can see below, the students had presumed to be the ones given opportunity to speak but this was all because of too much excitement</div>
<img src={client409s12} class=" d-block w-100" alt="Loading an image here...."  />

<img src={client409s13} class=" d-block w-100" alt="Loading an image here...."  />
<div><p></p>It was really noticed that Aswa's <span style={{color:"red"}}>presentation top had to extended beyond the tent as it looked a little smaller</span> for the crowd that gathered right behind him, <span style={{color:"red"}}>but as if this was not yet to the climax.</span></div>

<img src={client409s15} class=" d-block w-100" alt="Loading an image here...."  />
<div>Aswa above takes his opportunity to greet the students in preparation to present his package for the students, <span style={{color:"red"}}>he draws closer to his people because the jubilation had over powered the voice</span> produced by the microphone.</div>
<img src={client409s16} class=" d-block w-100" alt="Loading an image here...."  />
<div>In his move towards the wider crowd, there was another crowd that jubilated right behind him, <span style={{color:"red"}}>he looked back and still makes his move forward</span>. </div>
<img src={client409s17} class=" d-block w-100" alt="Loading an image here...."  />
<div><p></p>Little did he know that he was going to <span style={{color:"red"}}>receive another handkerchief usher-in from one lady who walked right straight</span> to him amidst all the silent crowd that was eager to hear a word.</div>
<img src={client409s19} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s20} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s22} class=" d-block w-100" alt="Loading an image here...."  />
<div><p></p>When he got the opportunity and silence to present himself, a statement was quoted that said, 'We are here for action and I am the commander in chief!'. He added that 'As parents hustle in your hustle, then we have to move together in presidence'.<p></p>He said that he doesn't not want to sugar coat any statements to the students but coming to know the fact that students hustle has to be respected. </div>
<img src={client409s21} class=" d-block w-100" alt="Loading an image here...."  />
<div><p></p>It was time for the students to be given opportunity to <span style={{color:"red"}}>ask any questions and only one question came from one student</span> by names of Silver and his statement was quoted, <span style={{color:"red",fontWeight:"bold"}}>"Mr. president, just confirm to us your names once again"</span> and his question was just backed up by a clapping from the crowd.</div>
<img src={client409s26} class=" d-block w-100" alt="Loading an image here...."  />
<div><p></p>Aswa on completing his presentation was quickly approached by another set of funds from the side of the crowd who even decided to escort him <span style={{color:"red"}}>off his feet by carrying him off the ground</span>. Was this because of excitement as well?</div>
<img src={client409s23} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s24} class=" d-block w-100" alt="Loading an image here...."  />
<img src={client409s25} class=" d-block w-100" alt="Loading an image here...."  />
<div>Aswa's speech was literally the last speech but was finally concluded by one of the university administrators who advised students not to damage university property i.e. walls with any scratches to maintain a descent campus and he said its usually costly to renovate damaged parts of the university. He asked camps to find other ways of may be hanging banners off the walls with other mechanisms. Shortly after his speech, music then sounded and the event was noticed to have closed with dances beeing seen from various camps and supporters. 

  <div style={{color:"red",fontWeight:"bold"}}>The event came to an end!</div>
</div>






</div>
  
    </div>)
  }
  
  return(
    <div>
<NuNav/>

      <Client id="client409s" 
      date="29th March, 2023"
      seeMore="/pages/nu/nutopstories"
       opinionsHeading1="students have reacted:"
        opinionsHeading2="students have posted. You too can post using the form below"
         opinionsHeading3="reactions below:" code={Code()}
          headline1="Aswa's open air campaign, Kampala Campus"
          
            formHeading="Post your reaction" formButtonLabel="Post"
     showCustomerContact="on"  recentCommentOnTop="on" />
    </div>
  )
}


