      export function MyRegisters(){
        const[status,setStatus]=useState('')
        const[goToRegisterStatus,setGoToRegisterStatus]=useState('')
        let data=""
        const[myRegisters,setMyRegisters]=useState('')

       
                return(<div>
      <AttendenceRegisterNav/>
          <div style={{padding:"20px"}}><div style={{color:"green", fontSize:"20px",fontWeight:"bold"}}>SEE YOUR REGISTERS</div>
          
                   
        <form method="post" id="myRegsitersForm" action="#">
     <div class="mb-3">
    
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter WhatsApp Contact e.g 0703852178'></input>
  
   
   <br></br>
     <input type="password" class="form-control" autoComplete="off" name="pin"  placeholder="Enter your PIN e.g. 12345 (5 digits)" ></input>
    
   
     </div>
     
     <div style={{fontSize:"17px",paddingBottom:"10px"}} dangerouslySetInnerHTML={{__html:status}}/>
  
     <div style={{borderRadius:"18px"}}  onClick={
       ()=>{
    
         if(Array.from(document.getElementById("myRegsitersForm").contact.value).length<10||Array.from(document.getElementById("myRegsitersForm").contact.value).length>10){
setStatus("<div style='color:red'>Please enter a correct contact......</div>")

}else if(Array.from(document.getElementById("myRegsitersForm").pin.value).length<5||Array.from(document.getElementById("myRegsitersForm").pin.value).length>5)
         {
            setStatus("<div style='color:red;'>Please enter a PIN of 5 digits ......</div>")
         }

else{
    setStatus("<div style='color:green;'>Getting your Registers, <span style='color:red;'>please wait ......</span></div>") 
    fetch('/verifyUser',{
        method:"post",
        headers:{'Content-type':'application/json'},
        body:JSON.stringify({
contact:document.getElementById("myRegsitersForm").contact.value,
pin:document.getElementById("myRegsitersForm").pin.value,
        }) 
    }).then(res=>res.json()).then((resp)=>{
             
        if(resp.registered==false){
            setStatus("<div style='color:red;'>You are not registered with Kayas, please register with Kayas</div>")
        }
         else{
            if(resp.registered==true&&resp.pin==false){
                setStatus(`<div style='color:green;'><span style='color:red;'>${resp.details.name},</span> your <span style='color:red;'>PIN is incorrect,</span> please try again......</div>`)
    
            }else{
          
       setStatus(`<div style='color:green;'><span style='color:red;'>${resp.details.name},</span> please be patient as we finalize with getting your Regsiters.........</div>`)

       fetch('/getMyRegisters',{
        method:"post",
        headers:{'Content-type':'application/json'},
        body:JSON.stringify({
          contact:parseInt(document.getElementById("myRegsitersForm").contact.value),
        })
    }).then(resp=>{
       return resp.json()}).then(resp=>{
if(resp.length==0){
  setStatus(`<div style='color:red;'>You don't have any Regsiters. Please create one. Thank you.</div>`) 

}else{
resp.reverse()
resp.forEach(register=>{
  data+=`<div class='col-sm-6 col-md-4'><div><a  style='color:black;' href='/pages/attendanceregs/${register.contact}/${register.registerId}'><span class="hoverEffectUnderline"><div  style='padding-bottom:10px;'><div style='color:green;font-size:18px;font-weight:bold;'>${register.registerTitle}</div><div>${register.attendees.length} contacts.</div><div style="font-size:15px;color:grey;">Register ID: ${register.registerId}</div><div style='color:grey;'>Tap here to view details.</div></div></span></a></div><div><a href="/pages/editattendanceregs/${register.contact}/${register.registerId}"><div class='btn btn-success btn-xs hovereffect'><span class='fa fa-edit'></span> Edit Register</div></a></div><hr></hr></div>`
})
setMyRegisters(data)
setStatus(`<div style='color:green;'>Done, please scroll down to see.</div>`)
}

          
         
          })
       
            }
            
          
        }
       
    }
        

    )
    
  
}
       }
     } class="btn btn-success hovereffect">Get your Registers</div>
    
     </form>
     <div style={{padding:"3px"}}class="row" dangerouslySetInnerHTML={{__html:myRegisters}}/>
     <hr></hr>
     </div>
     <div style={{padding:"20px"}}><div style={{color:"green", fontSize:"20px",fontWeight:"bold"}}>GO TO REGISTER</div>
          
                   
          <form method="post" id="goToRegsiterForm" action="#">
       <div class="mb-3">
      
      <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter Registrar contact'></input>
    
     
     <br></br>
       <input type="text" class="form-control" autoComplete="off" name="registerId"  placeholder="Enter register ID" ></input>
      
     
       </div>
       
       <div style={{fontSize:"17px",paddingBottom:"10px"}} dangerouslySetInnerHTML={{__html:goToRegisterStatus}}/>
    
       <div style={{borderRadius:"18px"}}  onClick={
         ()=>{
      
           if(Array.from(document.getElementById("goToRegsiterForm").contact.value).length<10||Array.from(document.getElementById("goToRegsiterForm").contact.value).length>10){
            setGoToRegisterStatus("<div style='color:red'>Please enter a correct contact......</div>")
  
  }else if(Array.from(document.getElementById("goToRegsiterForm").registerId.value).length<1)
           {
            setGoToRegisterStatus("<div style='color:red;'>Please enter a register ID......</div>")
           }
  
  else{
    setGoToRegisterStatus("<div style='color:green;'>Please wait................</div>")
    window.location.href=`/pages/attendanceregs/${parseInt(document.getElementById("goToRegsiterForm").contact.value)}/${document.getElementById("goToRegsiterForm").registerId.value}`
  }
         }
       } class="btn btn-success hovereffect">Go to Register</div>
      
       </form>
       
       <hr></hr>
       </div>
                  
       
        
                </div>)
              
      }