function Art(){
  let data=""
 
  const [dis,setDis]=useState('')
  const [comNumb,setNumb]=useState('')

  useEffect(()=>{
   

  fetch('/collection_campus_comments').then(res=>res.json()).then(res=>{
    setNumb(res.length)
    res.forEach(comment=>{
   
      data+='<div>'+comment.name+" "+comment.contact+"</div><div>"+comment.body+"</div><hr></hr>"
    
    })
    
    setDis(data);
      })
  
  },[])




  

  
  return (
    
     
      <div style={{background:"white"}}class="row">
         <div style={{padding:"15px"}}>  <div style={{fontSize:"20px",color:"red",paddingTop:"10px",textAlign:"center"}}>Top Stories Taking Place</div> </div>
       <div style={{padding:"20px"}}>
       <Anchor href="/pages/mubs/mubstopstories"  heading1="MUBS TOP STORIES" heading2="Get trending stories taking place in around MUBS as we provide them to you." caption="Tap here to see trending stories in Makerere..." />
       <Anchor href="/pages/muk/muktopstories"  heading1="MAKERERE TOP STORIES" heading2="Get trending stories taking place in around Makerere as we provide them to you." caption="Tap here to see trending stories in Makerere..." />
       <Anchor href="/pages/nu/nutopstories"  heading1="NDEJJE UNIVERSITY STORIES" heading2="Get trending stories taking place in Ndejje University as we provide them to you." caption="Tap here to see trending stories in Ndejje..." />
       <Anchor href="/pages/kyu/kyutopstories"  heading1="KYAMBOGO UNIVERSITY STORIES" heading2="Get trending stories taking place in Kyambogo University as we provide them to you." caption="Tap here to see trending stories in Kyambogo..." />
       <Anchor href="/pages/memeopinions"  heading1="FUN, MEMES AND LAUGHTERS" heading2="Have a look at memes and commedy stories and also share them through your whatsapp using the WhatsApp share button found at the bottom of each story." caption="Tap here to see and read detailed memes from Kayas..." />

       </div>
     <AllArticles/>
     
   
    </div>
  );  
}





export function ArtCompRead(props){
  let v="col-12 col-sm-12 col-md-6",phone=256703852178,link="https://api.whatsapp.com/send?phone="+props.phone+"&text=Hello%20"+props.name+",%20my%20comment%20about%20your%20article%20on%20Kayas%20Makerere%20University%20is;%20"
  return (

<div class={v} style={{paddingBottom:"10px"}}><p></p> <div style={{padding:"10px"}}><div className="row"> <div style={{textAlign:"center",paddingTop:"30px"}}class="col-6"><span style={{fontWeight:"bold",fontSize:"15px", color:"green"}}>{props.name}</span> <span style={{fontWeight:"",textAlign:"center",fontSize:"15px", color:"rgb(187, 9, 9)"}}>{props.post}</span>  <div> <a href={link}><button type="button" class="btn btn-sm btn1 btn-success"> <span class="fa fa-whatsapp"> </span> Your Comment?</button></a></div> </div> </div></div> <div style={{paddingLeft:"5px"}}>    <div  style={{color:"grey",textAlign:"center",fontFamily:"Charm",border:"0.5px solid grey",borderRadius:"20px",padding:"3px",overflow:"scroll"}}>{props.msg}</div>    </div></div>

  );
}




